define(['directives/directives'], function (directives) {
  'use strict';

  directives.initDirective('selectList', ['$timeout', '$compile', function (timeout, compile) {
    return {
      templateUrl: "../partials/select_list.html",
      restrict: 'A',
      scope: {
        source: "=",
        name: "=",
        titles: "="
      },
      link: function link(scope, element, attr) {
        var ready = function ready() {
          var sourceWatcher = function sourceWatcher(n, o, s) {
            var keyhistory = [];

            if (n) {
              var shiftPress = false,
                  ctrlPress = false;

              var shiftKeyUp = function shiftKeyUp(event) {
                if (event.keyCode == 16) {
                  shiftPress = false;
                  $("body").off("keyup.shift");
                  $("body").on("keydown.shift", shiftKeyDown);
                }
              };

              var ctrlKeyUp = function ctrlKeyUp(event) {
                if (event.keyCode == 91) {
                  ctrlPress = false;
                  $("body").off("keyup.ctrl");
                  $("body").on("keydown.ctrl", ctrlKeyDown);
                }
              };

              var shiftKeyDown = function shiftKeyDown(event) {
                if (event.keyCode == 16) {
                  shiftPress = true;
                  $("body").off("keydown.shift");
                  $("body").on("keyup.shift", shiftKeyUp);
                }
              };

              var ctrlKeyDown = function ctrlKeyDown(event) {
                if (event.keyCode == 91) {
                  ctrlPress = true;
                  $("body").off("keydown.ctrl");
                  $("body").on("keyup.ctrl", ctrlKeyUp);
                }
              };

              var bodyClick = function bodyClick(event) {
                s.$apply(function () {
                  for (var i in n) {
                    n[i].selected = false;
                  }
                });
              };

              $("body").on("keydown.shift", shiftKeyDown);
              $("body").on("keydown.ctrl", ctrlKeyDown);
              $("body").on("click.whole", bodyClick);

              scope.dropRight = function () {
                switchItem(true);
              };

              scope.dropLeft = function () {
                switchItem(false);
              };

              scope.leftFilter = function (elem) {
                return elem.applied != true && elem.leaveplace != true;
              };

              scope.rightFilter = function (elem) {
                return elem.applied == true && elem.leaveplace != true;
              };

              scope.rightFilterNoCheck = function () {
                var every = n.every(function (elem) {
                  return elem.applied != true || elem.selected != true;
                });
                return !every;
              };

              scope.leftFilterNoCheck = function () {
                var every = n.every(function (elem) {
                  return elem.applied == true || elem.selected != true;
                });
                return !every;
              };

              var switchItem = function switchItem(bool) {
                keyhistory = [];
                var checked = n.filter(function (elem) {
                  return elem.selected == true;
                });

                var loop = function loop(item) {
                  item.applied = bool;
                  item.selected = false;
                  item.renew = true;
                };

                for (var i in checked) {
                  loop(checked[i]);
                }
              };

              scope.leftToRight = function () {
                switchItem(true);
              };

              scope.rightToLeft = function () {
                switchItem(false);
              };

              var loop = function loop(item) {
                item.start = function () {
                  var self = this;

                  if (self.selected != true) {
                    self.selected = true;
                    keyhistory = [self];
                  }

                  ;

                  for (var i in n) {
                    if (n[i].selected == true) {
                      n[i].leaveplace = true;
                    }
                  }
                };

                item.stop = function () {
                  for (var i in n) {
                    if (n[i].leaveplace == true) {
                      n[i].leaveplace = false;
                    }
                  }
                };

                item.click = function () {
                  var self = this;
                  var checklist = n.filter(function (elem) {
                    if (self.applied) {
                      return self.applied == elem.applied;
                    } else {
                      return elem.applied == false || elem.applied == undefined;
                    }
                  });

                  for (var i in n) {
                    n[i].renew = false;
                  }

                  if (ctrlPress || shiftPress) {
                    if (shiftPress) {
                      keyhistory.$remove(function (index, elem) {
                        return elem == self;
                      });
                      keyhistory.push(self);

                      if (keyhistory.length > 1) {
                        var lastpress = keyhistory[keyhistory.length - 2];

                        var _addFn;

                        var loop = function loop(elem) {
                          if (_addFn == undefined) {
                            if (elem == self || elem == lastpress) {
                              elem.selected = true;

                              _addFn = function addFn(el) {
                                el.selected = true;

                                if (el == self || el == lastpress) {
                                  _addFn = undefined;
                                }
                              };
                            }
                          } else if (typeof _addFn == 'function') {
                            _addFn(elem);
                          }
                        };

                        for (var i in checklist) {
                          loop(checklist[i]);
                        }
                      }

                      ;
                    } else {
                      if (self.selected != true) {
                        self.selected = true;
                        keyhistory.push(this);
                      } else {
                        self.selected = false;
                        keyhistory.$remove(function (index, elem) {
                          return elem == this;
                        });
                      }
                    }
                  } else {
                    for (var i in n) {
                      n[i].selected = false;
                    }

                    self.selected = true;
                    keyhistory = [self];
                  }
                };
              };

              for (var i in n) {
                loop(n[i]);
              }
            }
          };

          scope.$watch("source", sourceWatcher);
        };

        timeout(ready);
      }
    };
  }]);
  directives.initDirective('selectDrag', ['$timeout', '$compile', function (timeout, compile) {
    return {
      restrict: 'A',
      scope: {
        target: "=",
        start: "&",
        stop: "&",
        name: "="
      },
      link: function link(scope, element, attr) {
        var ready = function ready() {
          var itemWatcher = function itemWatcher(n, o, s) {
            if (n) {
              var jqueryUiReady = function jqueryUiReady() {
                var helper = function helper(ui) {
                  var filter = n.filter(function (elem) {
                    return elem.selected == true;
                  }).map(function (elem) {
                    return elem[scope.name];
                  });
                  var result;

                  if (filter.length > 2) {
                    var rs = filter.slice(0, 2);
                    rs.push("等" + filter.length + "个");
                    result = $("<div></div>").text(rs.toString());
                  } else if (filter.length > 0) {
                    var rs = filter;
                    result = $("<div></div>").text(rs.toString());
                  } else if (filter.length == 0) {
                    var rs = $(ui.target);
                    result = $("<div></div>").append(rs);
                  }

                  result.attr("id", "select-helper");
                  result.addClass("item");
                  return result;
                };

                var start = function start() {
                  s.$apply(function () {
                    s.start();
                  });
                };

                var stop = function stop() {
                  s.$apply(function () {
                    s.stop();
                  });
                };

                $(element).draggable({
                  cursor: 'move',
                  cursorAt: {
                    top: 5,
                    left: 5
                  },
                  appendTo: 'body',
                  revert: true,
                  containment: 'body',
                  scroll: false,
                  revertDuration: 0,
                  zIndex: 9999999,
                  helper: helper,
                  start: start,
                  stop: stop
                });
              };

              $$.loadExternalJs(["jquery-ui"], jqueryUiReady);
            }
          };

          scope.$watch("target", itemWatcher);
        };

        timeout(ready);
      }
    };
  }]);
  directives.initDirective('selectDrop', ['$timeout', '$compile', function (timeout, compile) {
    return {
      restrict: 'A',
      scope: {
        target: "=",
        drop: "&"
      },
      link: function link(scope, element, attr) {
        var drop = function drop(event) {
          event.stopPropagation();
          scope.$apply(function () {
            scope.drop();
          });
        };

        var jqueryUiReady = function jqueryUiReady() {
          $(element).droppable({
            drop: drop
          });
        };

        $$.loadExternalJs(["jquery-ui"], jqueryUiReady);
      }
    };
  }]);
});